import React, { Component } from 'react';
import Fade from 'react-reveal/Fade';
import data from '../yourdata';

class Contact extends Component {
    state = {}
    render() {
        return (
            <div>
                <h1>
                    <Fade bottom cascade>Contact.</Fade>
                </h1>
                <Fade bottom>
                    <div className='contact-content'>
                        <div className='c1'>
                            <h1>
                                Let’s create your next<br></br>
                                <span className='amazing-color'>experience together</span>
                            </h1>
                            <a href={`mailto:${data.contactEmail}`} className='email'>{data.contactEmail}</a>
                            {/* <ul>
                                {data.social.map((link, index) => (
                                    <li key={index}><a target='_blank' rel="noopener noreferrer" href={link.url}>{link.name}</a></li>
                                ))}
                            </ul> */}
                        </div>
                        <div className='c2'>
                            <div class="badge-base LI-profile-badge" data-locale="en_US" data-size="large" data-theme="dark" data-type="HORIZONTAL" data-vanity="nishantvadgama" data-version="v1"></div>
                        </div>
                    </div>
                </Fade>
            </div>
        );
    }
}

export default Contact;