import React, { Component } from 'react';
import Fade from 'react-reveal/Fade';
import data from '../yourdata';

import { VerticalTimeline, VerticalTimelineElement } from 'react-vertical-timeline-component';
import 'react-vertical-timeline-component/style.min.css';

class Work extends Component {
    state = {}
    render() {
        return (<div>
            <h1 className='heading'>
                <Fade bottom cascade>Work.</Fade>
            </h1>
            <div className='timeline'>
                <VerticalTimeline>
                    {data.projects.map((project) => (
                        <VerticalTimelineElement
                            className="vertical-timeline-element--work"
                            date={project.date}
                            iconStyle={{ background: 'rgb(33, 150, 243)', color: '#fff' }}
                            icon={<img class="flag-size" alt='' src={project.icon}></img>}
                        >
                            <h3 className="vertical-timeline-element-title">{project.title}</h3>
                            <h4 className="vertical-timeline-element-subtitle">{project.service}</h4>
                            <p>{project.secription}</p>
                        </VerticalTimelineElement>
                    ))}
                </VerticalTimeline>
            </div>
        </div>);
    }
}

export default Work;